html, body {
	height              : 100vh;
	width               : 100vw;
	background          : #fff;
	-webkit-perspective : 1000px;
	perspective         : 1000px;
	overflow            : hidden;
	}

.menu-but {
	position : fixed;
	width    : 40px;
	top      : 10px;
	left     : 10px;
	cursor   : pointer;
	z-index  : 130;
	/* filter: invert(100%); */
	}

.menu-but {
	position   : absolute;
	width      : 40px;
	height     : 40px;
	background : #616161;
	top        : 10px;
	left       : 10px;
	cursor     : pointer;
	z-index    : 130;
	}

.menu-but:hover {
	background : #ccc;
	}

.menu-but.menu-bt-op .mile:nth-child(1) {
	transform : rotate(45deg) translateY(-1px) translateX(7px);
	height    : 100%;
	}

.menu-but.menu-bt-op .mile:nth-child(3) {
	transform : rotate(-45deg) translateY(-1px) translateX(-7px);
	height    : 100%;
	}

.menu-but.menu-bt-op .mile:nth-child(2) {
	transform : translateY(20px);
	}

.enter:before, .enter:after {
	position : absolute;
	}

.enter.hide:before {
	content          : '';
	width            : 1px;
	height           : 80px;
	opacity          : 1;
	background-color : #ccc;
	}

.enter.hide.hide1:before {
	bottom : 0;
	height : 150px;
	}

.enter.hide.hide2:before {
	bottom : 0;
	height : 150px;
	}

.enter.hide.hide2.hide3:before {
	opacity : 0;
	}

@-webkit-keyframes anim1 {
	0% {
		width : 0;
		}
	
	100% {
		width : 80px;
		}
	}

@keyframes anim1 {
	0% {
		width : 0;
		}
	
	100% {
		width : 80px;
		}
	}

.enter-text {
	bottom             : calc(20% + 40px);
	color              : #fff;
	width              : 150px;
	position           : absolute;
	z-index            : 100;
	left               : 50%;
	-webkit-transform  : translate(-50%, 0) scaleX(1);
	transform          : translate(-50%, 0) scaleX(1);
	opacity            : 1;
	-webkit-transition : all 200ms ease;
	transition         : all 200ms ease;
	}

.enter.hide:after {
	-webkit-transform : translate(-50%, 0) scaleX(0);
	transform         : translate(-50%, 0) scaleX(0);
	}

@-webkit-keyframes anim2 {
	0% {
		-webkit-transform : translate(-50%, -100%);
		transform         : translate(-50%, -100%);
		opacity           : 0;
		}
	
	50% {
		-webkit-transform : translate(-50%, -100%);
		transform         : translate(-50%, -100%);
		opacity           : 0;
		}
	
	100% {
		-webkit-transform : translate(-50%, 0);
		transform         : translate(-50%, 0);
		opacity           : 1;
		}
	}

@keyframes anim2 {
	0% {
		-webkit-transform : translate(-50%, -100%);
		transform         : translate(-50%, -100%);
		opacity           : 0;
		}
	
	50% {
		-webkit-transform : translate(-50%, -100%);
		transform         : translate(-50%, -100%);
		opacity           : 0;
		}
	
	100% {
		-webkit-transform : translate(-50%, 0);
		transform         : translate(-50%, 0);
		opacity           : 1;
		}
	}

.linehand, .linehand nav {
	position           : absolute;
	height             : 100vh;
	width              : 100vw;
	overflow           : hidden;
	z-index            : 120;
	-webkit-transition : 1s ease all;
	transition         : 1s ease all;
	}

.linehand {
	opacity    : 0;
	visibility : hidden;
	/* z-index: -22; */
	}

.linehand nav ul {
	display               : -webkit-box;
	display               : -ms-flexbox;
	display               : flex;
	-webkit-box-orient    : horizontal;
	-webkit-box-direction : normal;
	-ms-flex-direction    : row;
	flex-direction        : row;
	-ms-flex-pack         : distribute;
	justify-content       : space-around;
	height                : 150px;
	bottom                : 0;
	position              : absolute;
	overflow              : visible;
	width                 : 100%;
	left                  : 50%;
	-webkit-box-align     : end;
	-ms-flex-align        : end;
	align-items           : flex-end;
	visibility            : hidden;
	-webkit-transform     : translateX(-50%);
	transform             : translateX(-50%);
	}

.linehand nav li {
	height  : 100%;
	padding : 0 10%;
	cursor  : pointer;
	}

.linehand.l-2-s, .linehand.l-3-s {
	opacity    : 1 !important;
	visibility : visible;
	z-index    : 120;
	}

.linehand.l-3-s nav li {
	height     : 50%;
	opacity    : 1;
	visibility : visible;
	}

.linehand nav li:not(.li-open):hover {
	height : 80%;
	}

.linehand nav li:not(.li-open):hover a {
	letter-spacing : normal;
	}

.linehand nav ul li:hover span.line {
/ / height: 120 %;
	}

.linehand nav ul li span.line {
	width       : 1px;
	height      : 100%;
	position    : absolute;
	border-left : 1px solid #ccc;
	}

.linehand.l-1-s ul {
	width      : 1px;
	visibility : hidden;
	}

.linehand.l-2-s ul, .linehand.l-3-s ul, .linehand ul {
	width      : 1px;
	visibility : hidden;
	}

.linehand ul a {
	position                 : absolute;
	-webkit-transform-origin : left top;
	transform-origin         : left top;
	color                    : #fff;
	-webkit-transform        : rotate(0deg) translateY(-150%) translateX(8px);
	transform                : rotate(0deg) translateY(-150%) translateX(8px);
	text-decoration          : none;
	font-size                : 17px;
	opacity                  : 0;
	-webkit-transition       : 500ms ease all;
	transition               : 500ms ease all;
	letter-spacing           : 7px;
	}

.linehand.l-3-s ul a {
	opacity           : 1;
	-webkit-transform : rotate(-90deg) translateY(-80%) translateX(8px);
	transform         : rotate(-90deg) translateY(-80%) translateX(8px);
	}

.linehand.l-2-s ul {
	width      : 100%;
	visibility : visible;
	}

.linehand.l-3-s ul {
	width      : 100%;
	height     : 50%;
	visibility : visible;
	overflow   : visible;
	}

.linehand.l-3-s.linehand-clicked ul {
	height : 100vh;
	}

.linehand nav ul li span.line, .linehand nav li, .linehand nav ul {
	-webkit-transition : 500ms ease all;
	-webkit-transition : all 500ms ease 0ms;
	transition         : all 500ms ease 0ms;
	}

.linehand.l-0-s {
	display : none;
	}

.linehand.l-3-s.linehand-clicked li:not(.li-clicked) {
	height : 0;
	}

.linehand.l-3-s.linehand-clicked li:not(.li-clicked) a {
	-webkit-transform : rotate(90deg) translateY(10px) translateX(18px);
	transform         : rotate(90deg) translateY(10px) translateX(18px);
	}

li.li-clicked {
	height : 100vh !important;
	top    : 0;
	}

.linehand nav ul li.li-clicked.li-open span {
	width      : 200vw;
	background : #ccc;
	z-index    : 1000;
	}

li.li-clicked.li-open {
	cursor            : default;
	-webkit-transform : translateX(-100vw);
	transform         : translateX(-100vw);
	}

.enter:before {
	content                     : '';
	width                       : 80px;
	height                      : 80px;
	bottom                      : 20%;
	left                        : 50%;
	z-index                     : 105;
	opacity                     : 1;
	-webkit-transform           : translateX(-50%);
	transform                   : translateX(-50%);
	background-color            : #ffffff;
	cursor                      : pointer;
	-webkit-transition          : 300ms 200ms ease;
	transition                  : 300ms 200ms ease;
	-webkit-transition-property : width, top, bottom, height, -webkit-transform;
	transition-property         : width, top, bottom, height, -webkit-transform;
	transition-property         : width, transform, top, bottom, height;
	transition-property         : width, transform, top, bottom, height, -webkit-transform;
	-webkit-animation           : anim4 4s ease;
	animation                   : anim4 4s ease;
	-webkit-perspective         : 10px !important;
	perspective                 : 10px !important;
	-webkit-transform-origin    : center center 0px;
	transform-origin            : center center 0px;
	}

@keyframes anim1 {
	0% {
		width : 0;
		}
	
	100% {
		width : 80px;
		}
	}

.enter.hide:after {
	-webkit-transform : translate(-50%, 0) scaleX(0);
	transform         : translate(-50%, 0) scaleX(0);
	}

@keyframes anim2 {
	0% {
		-webkit-transform : matrix3d(-0.00, 0, 1, 0.001, 0.00, 1, 0.00, 0, -0.98, 0, -0.17, 0, 0, 0, 0, 1);
		transform         : matrix3d(-0.00, 0, 1, 0.001, 0.00, 1, 0.00, 0, -0.98, 0, -0.17, 0, 0, 0, 0, 1);
		}
	
	50% {
		-webkit-transform : matrix3d(-0.00, 0, 1, 0.001, 0.00, 1, 0.00, 0, -0.98, 0, -0.17, 0, 0, 0, 0, 1);
		transform         : matrix3d(-0.00, 0, 1, 0.001, 0.00, 1, 0.00, 0, -0.98, 0, -0.17, 0, 0, 0, 0, 1);
		}
	
	100% {
		-webkit-transform : matrix3d(-1, 0, 1, 0, 0.00, 1, 0.00, 0, -0.98, 0, -0.17, 0, 0, 0, 0, 1);
		transform         : matrix3d(-1, 0, 1, 0, 0.00, 1, 0.00, 0, -0.98, 0, -0.17, 0, 0, 0, 0, 1);
		}
	}

@-webkit-keyframes anim4 {
	0% {
		-webkit-transform : translateX(-50%) rotateY(90deg);
		transform         : translateX(-50%) rotateY(90deg);
		}
	
	50% {
		-webkit-transform : translateX(-50%) rotateY(90deg);
		transform         : translateX(-50%) rotateY(90deg);
		}
	
	100% {
		-webkit-transform : rotateY(0deg) translateX(-50%);
		transform         : rotateY(0deg) translateX(-50%);
		}
	}

@keyframes anim4 {
	0% {
		-webkit-transform : translateX(-50%) rotateY(90deg);
		transform         : translateX(-50%) rotateY(90deg);
		}
	
	50% {
		-webkit-transform : translateX(-50%) rotateY(90deg);
		transform         : translateX(-50%) rotateY(90deg);
		}
	
	100% {
		-webkit-transform : rotateY(0deg) translateX(-50%);
		transform         : rotateY(0deg) translateX(-50%);
		}
	}

body.en a:before, body.en p:before {
	content : attr(data-en);
	}

body.fa a:after, body.fa p:after {
	content     : attr(data-fa);
	font-family : 'Vazir', tahoma;
	}

