	width                 : 100vw;
	box-sizing            : border-box;
	display               : -webkit-box;
	display               : -ms-flexbox;
	display               : flex;
	-webkit-box-align     : center;
	-ms-flex-align        : center;
	align-items           : center;
	-ms-flex-pack         : distribute;
	justify-content       : space-around;
	opacity               : 1;
	-webkit-box-orient    : horizontal;
