	transition               : 1s ease all;
	position                 : relative;
	-webkit-transform-origin : center center 0px;
	transform-origin         : center center 0px;
	-webkit-perspective      : 100px;
	perspective              : 100px;
	-webkit-animation        : anim2 2s ease;
	animation                : anim2 2s ease;
	}

