/*Projects*/
.projects-container {
	width             : 100%;
	height            : 100%;
	display           : -webkit-box;
	display           : -ms-flexbox;
	display           : flex;
	-webkit-box-align : start;
	-ms-flex-align    : start;
	align-items       : flex-start;
	}

.projects-cat-container {
	width                 : 30%;
	height                : 100%;
	box-sizing            : border-box;
	padding-right         : 30px;
	display               : -webkit-box;
	display               : -ms-flexbox;
	display               : flex;
	-webkit-box-orient    : vertical;
	-webkit-box-direction : normal;
	-ms-flex-direction    : column;
	flex-direction        : column;
	-webkit-box-pack      : center;
	-ms-flex-pack         : center;
	justify-content       : center;
	-webkit-box-align     : end;
	-ms-flex-align        : end;
	align-items           : flex-end;
	text-align            : right;
	}

.projects-container.awards-container .projects-cat-container {
	width : 50%;
	}

.projects-container.awards-container .projects-box-container {
	width : 50%;
	}

.projects-cat-h2 {
	font-size : 50px;
	padding   : 20px 0;
	}

.projects-cat-li {
	line-height        : 33px;
	cursor             : pointer;
	-webkit-transition : 500ms ease;
	transition         : 500ms ease;
	}

.projects-cat-li:hover, li.projects-cat-li.clicked {
	color          : #ccc;
	letter-spacing : 10px;
	}

.projects-box-container {
	width                      : 70%;
	max-height                 : 100%;
	display                    : -webkit-box;
	display                    : -ms-flexbox;
	display                    : flex;
	-ms-flex-wrap              : wrap;
	flex-wrap                  : wrap;
	overflow-x                 : hidden;
	-webkit-perspective        : 1000px;
	perspective                : 1000px;
	overflow-y                 : auto;
	-webkit-overflow-scrolling : touch;
	}

.projects-box {
	width               : calc((100% / 4) - 22px);
	padding             : 0 20px 20px 0px;
	/* background: url("../img/box-bg.svg") center center no-repeat; */
	background-size     : contain;
	overflow            : visible;
	-webkit-perspective : 100px;
	perspective         : 100px;
	-webkit-transition  : 500ms cubic-bezier(0.4, 0, 1, 1) all;
	transition          : 500ms cubic-bezier(0.4, 0, 1, 1) all;
	}

.projects-box-square {
	position           : relative;
	width              : 100%;
	height             : 0;
	padding-bottom     : 100%;
	overflow           : visible;
	-webkit-transform  : scaleX(0);
	transform          : scaleX(0);
	-webkit-transition : all 1000ms ease;
	transition         : all 1000ms ease;
	}

.projects-box-square.loaded {
	-webkit-transform : scaleX(1);
	transform         : scaleX(1);
	}

.projects-container .projects-img {
	position           : absolute;
	width              : 100%;
	height             : 100%;
	top                : 0;
	left               : 0;
	opacity            : 1;
	-webkit-transition : all 1000ms ease;
	transition         : all 1000ms ease;
	-webkit-transform  : rotateX(0deg) rotateY(0deg) rotateZ(0deg);
	transform          : rotateX(0deg) rotateY(0deg) rotateZ(0deg);
	}

.projects-box:hover .projects-img {
	opacity           : 0;
	-webkit-transform : rotateX(0deg) rotateY(180deg) rotateZ(0deg);
	transform         : rotateX(0deg) rotateY(180deg) rotateZ(0deg);
	}

.events-box-container .projects-box:hover .projects-img {
	transform : rotateX(180deg) rotateY(0deg) rotateZ(0deg);
	}

.projects-title {
	position           : absolute;
	width              : 100%;
	height             : 100%;
	top                : 0;
	left               : 0;
	color              : #FFFFFF;
	font-size          : 20px;
	flex-direction     : column;
	display            : flex;
	justify-content    : space-around;
	align-items        : center;
	opacity            : 0;
	-webkit-transform  : rotateX(0deg) rotateY(-180deg) rotateZ(0deg);
	transform          : rotateX(0deg) rotateY(-180deg) rotateZ(0deg);
	-webkit-transition : all 1000ms ease;
	transition         : all 1000ms ease;
	}

.projects-box:hover .projects-title {
	opacity           : 1;
	-webkit-transform : rotateX(0deg) rotateY(0deg) rotateZ(0deg);
	transform         : rotateX(0deg) rotateY(0deg) rotateZ(0deg);
	}

/*Project*/
.project-container {
	width                      : 100%;
	height                     : 100%;
	overflow-x                 : hidden;
	overflow-y                 : auto;
